import opg from 'ts/opg.ts';
import Table from 'ts/ui/Table.ts';
import {Languages} from 'ts/util/Languages';
import AddMatchMeeting from './AddMatchMeeting';
import {api} from '../../../ts/util/api';


opg.api({
	getMeetings: 'focusonmeetinginfo/GetAllFocusOnMeetingInfo',
	delMeetingById: 'focusonmeetinginfo/DelFocusOnMeetingInfo',
	delMeetingUserById: 'focusonmeetinginfo/DelFocusOnMeetingBuyerByBarCode',
	getBuyersAndExhibitorsByMeetingId: 'focusonmeetinginfo/GetFocusOnMeetingBuyerAndExhibitionsById',
	autoMatchExhibitor: 'focusonmeetinginfo/AutoMatchFocusOnMeetingExhibtions',
	newAutoMatchExhibitor: 'focusonmeetinginfo/NewAutoMatchFocusOnMeetingExhibtions',
    autoMatchSchedule: 'focusonmeetinginfo/AutoMatchScheduleExhibitions',
	'addBuyers!post': 'focusonmeetinginfo/AddFocusOnMeetingInfoBuyers',
});

const lng = Languages.package;
let key = '';
let focusOnMeetingInfoId = '';

jQuery.datetimepicker.setLocale(Languages.current);

let tb: Table = opg('#tb1').table({
	api: opg.api.getMeetings,
	titleBar: {
		title: lng.moduleName,
		buttons: [{id: 'btnAdd', className: 'btn-create', html: `<i class="ico ico-create"></i> ${lng.add}`}],
	},
	columns: [
		{
			text: lng.serial,
			width: 60,
			src: 'id',
		},
		{
			text: lng.startTime,
			width: 150,
			src: 'begindate',
		},
		{
			text: lng.endTime,
			width: 150,
			src: 'enddate',
		},
		{
			text: lng.meetingName,
			src: 'name',
		},
		{
			text: lng.place,
			src: 'address',
		},
		{
			text: `${lng.process}`,
			src: 'id',
			width: 200,
			render: function (id, i, row) {
				//console.log(arr);
				return `
						<button class="btn-mini btn-info btnSelect" data-id="${id}">${lng.select}</button> 
						<button class="btn-mini btn-danger btnDelete" data-id="${id}">${lng.del}</button>
				`;
			},
		},
	],
	pagination: false,
});



$('#btnAdd').click(function () {
	new AddMatchMeeting(lng, tb);
});

const matchingDiv = $('#matchingDiv');

tb.tbody.on('click', '.btnSelect', function () {
	tb.tbody.find('.greenTr').removeClass('greenTr');
	let tr = $(this).closest('tr');
	tr.addClass('greenTr');

	focusOnMeetingInfoId = this.getAttribute('data-id');
	matchingDiv.hide();
	api.getBuyersAndExhibitorsByMeetingId({focusOnMeetingInfoId}, data => {
		key = '';
		tbBuyer.update(data.buyers);
		tbExhibitor.update(data.exhibtions);
		matchingDiv.show();
	});
});


tb.tbody.on('click', '.btnDelete', function () {
	let btn = this;
	opg.confirm(lng.askDeleteMeeting, function () {
		api.delMeetingById({id: btn.getAttribute('data-id')}, res => {
			tb.update();
		});
	});

});

let tbBuyer: Table = opg('#tbBuyer').table({
	data: [],
	titleBar: {
		title: lng.buyer,
		buttons: [{id: 'btnFilterBuyer', className: 'btn-warning', html: `${lng.filterBuyers}`}],
	},
	columns: [
		{
			text: `${lng.roleName}`,
			src: 'begintime',
			width: 100,
			render: a => lng.buyer,
		},
		{
			text: `${lng.barcode}`,
			src: 'barcode',
			width: 150,
		},
		{
			text: `${lng.name}`,
			src: 'name',
			width: 150,
		},
		{
			text: `${lng.company}`,
			src: 'company',
		},
		{
			text: lng.process,
			src: 'barcode',
			width: 90,
			render: (barcode, index, row) => {
				return `<button data-barcode="${barcode}" data-index="${row[':index']}" class="btn-mini btn-danger btnDelete">${lng.del}</button>  `;
			},
		},
	],
});

tbBuyer.tbody.on('click', '.btnDelete', function (e) {
	let btn = this;
	opg.confirm(lng.askDeleteMeetingUser, function () {
		api.delMeetingUserById({barcode: e.target.getAttribute('data-barcode') , focusOnMeetingInfoId}, res => {
			opg.ok(lng.processSuccess);
			api.getBuyersAndExhibitorsByMeetingId({focusOnMeetingInfoId}, data => {
				tbBuyer.update(data.buyers);
				tbExhibitor.update(data.exhibtions);
			});
		});
	});

});

const buyerPage = __uri('./filterBuyer.html');

$('#btnFilterBuyer').click(function () {
	let pop = opg.popTop(`<iframe src="${buyerPage}">`, {
		title: lng.filterBuyers,
		width: 900,
		height: 600,
		buttons: {
			ok: {
				text: lng.add,
				className: 'btn-success',
				onClick: function (i, iframe) {
					let buyers = iframe.getTbData();
					if(buyers.length){
						api.addBuyers({key , buyers}, res=>{
							key = res;
							api.getBuyersAndExhibitorsByMeetingId({focusOnMeetingInfoId}, data => {
								tbBuyer.update(data.buyers);
								tbExhibitor.update(data.exhibtions);
							});
							pop.close();
						})
					}
					console.log(buyers);
					return true;
				},
			},
			cancel: lng.cancel,
		},
	});
});


let tbExhibitor: Table = opg('#tbExhibitor').table({
	data: [],
	titleBar: {
		title: lng.exhibition,
	},
	columns: [
		{
			text: `${lng.roleName}`,
			src: 'begintime',
			width: 100,
			render: a => lng.exhibition,
		},
		{
			text: `${lng.barcode}`,
			src: 'barcode',
			width: 150,
		},
		{
			text: `${lng.name}`,
			src: 'name',
			width: 150,
		},
		{
			text: `${lng.company}`,
			src: 'company',
		},
	],
});

$('#btnAutoMatchExhibitor').click(function () {
	if(!key){
		opg.warn(lng.warnAddUser);
	}
	else{
		api.autoMatchExhibitor({focusOnMeetingInfoId , key} , res=>{
			opg.ok(lng.processSuccess);
			api.getBuyersAndExhibitorsByMeetingId({focusOnMeetingInfoId}, data => {
				tbBuyer.update(data.buyers);
				tbExhibitor.update(data.exhibtions);
			});
		})
	}
});

$('#newAutoMatchExhibitor').click(function () {
    if(!key){
        opg.warn(lng.warnAddUser);
    }
    else{
        api.newAutoMatchExhibitor({focusOnMeetingInfoId , key} , res=>{
            opg.ok(lng.processSuccess);
            api.getBuyersAndExhibitorsByMeetingId({focusOnMeetingInfoId}, data => {
                tbBuyer.update(data.buyers);
                tbExhibitor.update(data.exhibtions);
            });
        })
    }
});




$('#btnAutoMatchSchedule').click(function () {
	api.autoMatchSchedule(res=>{
        opg.ok(lng.processSuccess);
	});
});